.App {
  background-color: #ccdddd;
  height: fit-content;
  width: 100vw;
  min-height: 100%;
  min-width: 100%;
  flex-direction: column;
  justify-content: center !important;
  align-items: center;
  max-width: 100%;
  justify-content: center;
  align-items: start;
}

.contentContainer {
  height: fit-content;
  width: 100vw;
  padding-bottom: 10%;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  overflow: hidden;
}

.navOffset {
  height: fit-content;
  width: 100vw;
  background-color: lightcyan;
  position: absolute;
  top: 0;
  left: 0;
  height: 10vh;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  min-height: 40px;
}

.footerOffset {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: fit-content;
}

.account-sign-up {
  text-align: center;
  font-size: 20px;
  padding-bottom: 20px
}

@media only screen and (min-height: 400px) {
  .contentContainer {
    padding: 16vh 0;
  }
  .navOffset {
    height: 10vh;
  }
}
